//reduce the div scroll width
.t-scroll-area-div::-webkit-scrollbar {
  width: 5px !important;
}

.t-scroll-area-div::-webkit-scrollbar-track {
  background: #f1f1f1 !important;
}

.t-scroll-area-div::-webkit-scrollbar-thumb {
  background: #888 !important;
}

.t-scroll-area-div::-webkit-scrollbar-thumb:hover {
  background: #555 !important;
}

.t-phonebook-item {
  padding: 10px;
  border-bottom: 1px solid #ccc;
  cursor: pointer;
}

.t-phonebook-item:hover {
  background-color: #f5f5f5;
  color: black !important;
}

.t-phonebook-item .t-phonebook-item-name {
  font-weight: bold;
}

.t-phonebook-item .t-phonebook-item-number {
  font-size: 12px;
  color: #999;
}

.t-phonebook-item .t-phonebook-item-number span {
  font-weight: bold;
}

.t-phonebook-scrolled-to-bottom-message {
  padding: 10px;
  text-align: center;
}

.t-phonebook-search {
  padding: 10px;
}

.t-phonebook-search input {
  border: none;
  outline: none;
  width: 100%;
}

.t-phonebook-search input:focus {
  border: none;
  outline: none;
  box-shadow: 0px 0px 3px -1px black !important;
}

.t-phonebook {
  background: transparent;
  padding: 10px 0px 10px 0px;
  width: 180px;
  display: block;
  color: black;
  font-size: 15px;
  text-align: center;
  margin-top: 10px;
  margin-left: 29px;
  border-radius: 6px;
  cursor: pointer;
  border: 1px solid black !important;
}

.t-phonebook:hover {
  background: #cdcccc;
  color: black !important;
}
